import React, { useState, useEffect, useMemo } from "react";
import "./POC.css";
import title from "./images/mf-title.png";
import connectBtn from "./images/mf-connect-btn.png";
import buyBtn from "./images/mf-buy.png";
import mailBtn from "./images/mf-mail.png";
import buyPopupBg from "./images/mf-buy-stamp-bg.png";
import mailPopupBg from "./images/mf-mail-letters-bg.png";
import buyPopupBtn from "./images/mf-buy-now.png";
import mailPopupBtn from "./images/mf-use-stamp.png";
import xIcon from "./images/X.svg";
import { connect, isConnected, getLocalStorage, openContractCall, disconnect } from '@stacks/connect';
import { bufferCVFromString, uintCV, Pc, principalCV } from "@stacks/transactions";
import { STACKS_TESTNET, defaultUrlFromNetwork } from "@stacks/network";

function POC() {
  const [wallet, setWallet] = useState([]);
  const [letters, setLetters] = useState([]);
  const [buying, setBuying] = useState(false);
  const [mailing, setMailing] = useState(false);
  const [walletConnected, setWalletConnected] = useState(isConnected());
  const [userAddresses, setUserAddresses] = useState([]);

  const network = useMemo(() => STACKS_TESTNET, []);
  
  const wallets = useMemo(() => network === STACKS_TESTNET ? {
    'mailflow': 'STNBT7T5FN8W81ETXSAJJRTR9W39YGXJTFR7V5WT',
    'user1': 'ST2VZ4RKK8T32PJ1EQ83Q6JRYEVFFW6JB35WAMGJD',
  } : {
    'mailflow': 'ST1PQHQKV0RJXZFY1DGX8MNSNYVE3VGZJSRTPGZGM',
    'user1': 'ST1SJ3DTE5DN7X54YDH5D64R3BCB6A2AG2ZQ8YPD5',
  }, [network]);

  const contractAddress = useMemo(() => wallets['mailflow'], [wallets]);
  const tokenName = 'phase7token';

  useEffect(() => {
    if (isConnected()) {
      const data = getLocalStorage();
      setUserAddresses(data.addresses['stx']);
      setWalletConnected(true);
    }
  }, [])

  const connectWallet = async () => {
    if (!isConnected()) {
      try {
        const response = await connect({ forceWalletSelect: true, network: network === STACKS_TESTNET ? 'testnet' : 'devnet' });
        console.log('Connected addresses:', response.addresses);
        setUserAddresses(response.addresses);
        setWalletConnected(true);
      } catch (e) {
        console.error('Error:', e);
      }
    } else {
      const data = getLocalStorage();
      console.log('Already connected:', data.addresses);
      setUserAddresses(data.addresses['stx']);
      setWalletConnected(true);
    }
  }

  const disconnectWallet = async () => {
    if (isConnected()) {
      disconnect();
      setWalletConnected(false);
    }
  }

  const waitForConfirmation = async (txId, network) => {
    for (let attempts = 0; attempts < 300; attempts++) {
      const response = await fetch(`${defaultUrlFromNetwork(network)}/extended/v1/tx/${txId}`);
      const txInfo = await response.json();

      if (txInfo.tx_status === 'success' || txInfo.tx_status === 'abort_by_response') {
        return txInfo;
      }
      await new Promise(resolve => setTimeout(resolve, 1000));
    }
    throw new Error('Transaction confirmation timeout');
  }

  const openTokenContractCall = (contractName, functionName, functionArgs, postConditions) => {
    return new Promise((resolve, reject) => {
      const onFinish = async (tx) => {
        console.log('Transaction sent:', tx.txId);
        try {
          const receipt = await waitForConfirmation(tx.txId, network);
          console.log(receipt);
          resolve(receipt);
        } catch (e) {
          reject(e);
        }
      }

      try {
        const options = {
          contractAddress,
          contractName,
          functionName,
          functionArgs,
          network,
          appDetails: {
            name: 'MailFlow',
            icon: 'https://example.com/icon.png'
          },
          onFinish,
          postConditions,
          postConditionMode: 'deny', 
        };

        openContractCall(options);
      } catch (e) {
        reject(e);
      }
    });
  }

  const buyStamp = async (uuid = crypto.randomUUID()) => {
    // post-conditions for @stack/connect transaction
    const postConditionSTX = Pc
      .principal(userAddresses.filter(x => x.symbol === 'STX')[0].address)
      .willSendEq(75000000n)
      .ft(contractAddress + '.usdh-mock-v2', 'usdh-mock-v2');

    const postConditionStamp = Pc
      .principal(contractAddress)
      .willSendEq(1n)
      .ft(contractAddress + '.' + tokenName, tokenName);

    return openTokenContractCall(
      tokenName, 
      'buy-stamp', 
      [uintCV(75000000), bufferCVFromString(uuid)], 
      [postConditionSTX, postConditionStamp]
    );
  }

  const mailStamp = async (uuid = crypto.randomUUID()) => {
    // post-conditions for @stack/connect transaction
    const postConditionStamp = Pc
      .principal(userAddresses.filter(x => x.symbol === 'STX')[0].address)
      .willSendEq(1n)
      .ft(contractAddress + '.' + tokenName, tokenName);


    const postConditionStamp2 = Pc
      .principal(contractAddress)
      .willSendEq(1n)
      .ft(contractAddress + '.' + tokenName, tokenName);

    return openTokenContractCall(
      tokenName, 
      'mail-stamp', 
      [bufferCVFromString(uuid)], 
      [postConditionStamp, postConditionStamp2]
    );
  }
  
  const mintUSDHStamp = async () => {
    return openTokenContractCall(
      'usdh-mock-v2', 
      'mint', 
      [uintCV(10000000000), principalCV(userAddresses.filter(x => x.symbol === 'STX')[0].address)]
    );
  }

  // Handlers for buttons
  const handleBuyStamp = async () => {
    try {
      const stampID = crypto.randomUUID();
      const releaseRes = await buyStamp(stampID);
      
      setWallet([...wallet, stampID]);
    } catch (e) {
      console.log(e);
    }
    setBuying(false);
  };

  const handleMailStamp = async () => {
    if (wallet.length === 0) {
      alert("Please buy some stamps!");
      return;
    }
    
    const stampId = wallet[wallet.length - 1];

    const mailRes = await mailStamp(stampId);

    setWallet([...wallet?.slice(0, wallet.length - 1)]);

    setLetters([...letters, stampId]);
    setMailing(false);
  };

  return (
    <div className="POC">
      <div className="phone-wrap">
        <div className={`popup ${buying || mailing ? "show" : ""}`}>
          {buying ?
          <div className="popup-inner">
            <img src={buyPopupBg} alt="buy popup bg" width={292} height={292}/>
            <img src={xIcon} className="x-icon" alt="x icon" width={12} height={12} onClick={() => setBuying(false)}/>
            <img src={buyPopupBtn} className="btn" alt="buy popup btn" width={140} height={52} onClick={handleBuyStamp}/>
          </div> :
          mailing ?
          <div className="popup-inner">
            <img src={mailPopupBg} alt="mail popup bg" width={292} height={292}/>
            <img src={xIcon} className="x-icon" alt="x icon" width={12} height={12} onClick={() => setMailing(false)}/>
            <img src={mailPopupBtn} className="btn" alt="mail popup btn" width={140} height={52} onClick={handleMailStamp}/>
          </div> : null
          }
        </div>

        <div className="title">
          <img src={title} alt="title"/>
        </div>
        
        {!walletConnected ?
        <div className="connect-section">
          <p>Connect to a Stacks wallet!</p>
          <div className="connect-btn btn" onClick={() => connectWallet()}>
            <img src={connectBtn} alt="connect"/>
          </div>
        </div>
        :
        <>
        <div className="top-btns">
          <div>
            <button onClick={mintUSDHStamp}>Get USDh</button>
          </div>
          <div>
            <button onClick={disconnectWallet}>Disconnect Wallet</button>
          </div>
        </div>

        <div className="info-middle">
          {/* Wallet Section */}
          <div style={{ marginBottom: "20px" }}>
            <h2>Stamps</h2>
            {wallet === undefined || wallet.length === 0 ? (
              <p>No stamps in your wallet.</p>
            ) : (
              <ul>
                {wallet.map((id) => (
                  <li key={id}>{id}</li>
                ))}
              </ul>
            )}
          </div>

          {/* Letters Section */}
          <div>
            <h2>Letters</h2>
            {letters === undefined || letters.length === 0 ? (
              <p>No letters sent yet.</p>
            ) : (
              <ul>
                {letters.map((id) => (
                  <li key={id}>{id}</li>
                ))}
              </ul>
            )}
          </div>
        </div>

        {/* Buttons Section */}
        <div className="buttons">
          <div className="buy-btn btn" onClick={() => setBuying(true)}>
            <img src={buyBtn} alt="buy"/>
          </div>

          <div className="mail-btn btn" onClick={() => setMailing(true)}>
            <img src={mailBtn} alt="mail"/>
          </div>
        </div> 
        </>
        }
      </div>
    </div>
  );
}

export default POC;
