import React, { useState } from "react";
import "./TestnetMockup.css";

function TestnetMockup() {
  const [activeUser, setActiveUser] = useState("user1");
  const [transferReceiver, setTransferReceiver] = useState("user1");
  const [wallet, setWallet] = useState({'user1': [], 'user2': [], 'user3': [], 'user4': [], 'user5': []});
  const [letters, setLetters] = useState({'user1': [], 'user2': [], 'user3': [], 'user4': [], 'user5': []});
  const [transactions, setTransactions] = useState({'user1': [], 'user2': [], 'user3': [], 'user4': [], 'user5': []});
  const wallets = {
    'mailflow': 'STNBT7T5FN8W81ETXSAJJRTR9W39YGXJTFR7V5WT',
    'user1': 'ST2VZ4RKK8T32PJ1EQ83Q6JRYEVFFW6JB35WAMGJD',
    'user2': 'ST2KCZ5AMWJ46DC2R55G5AKXQSRATCMB629AQ46D1'
  }
  
  const baseURL = 'http://localhost:3001';
  const mintStamp = async () => {
    const response = await fetch(`${baseURL}/mint`);
    return response.json();
  }

  const transferStamp = async (sender, receiver, uuid = crypto.randomUUID()) => {
    const response = await fetch(`${baseURL}/transfer/${sender}/${receiver}/${uuid}`);
    return response.json();
  }

  const burnStamp = async (uuid) => {
    const response = await fetch(`${baseURL}/burn/${uuid}`);
    return response.json();
  }

  const handleSelectUser = async (event) => {
    setActiveUser(event.target.value);
  };

  const handleSelectReceiver = async (event) => {
    setTransferReceiver(event.target.value);
  };

  // Handlers for buttons
  const handleMailStamp = async () => {
    if (wallet[activeUser].length === 0) {
      alert("Please buy some stamps!");
      return;
    }
    
    const stampId = wallet[activeUser][wallet[activeUser].length - 1];

    const transferRes = await transferStamp(wallets[activeUser], wallets['mailflow'], stampId);
    const burnRes = await burnStamp(stampId);

    setWallet((prev) => ({
      ...prev,
      [activeUser]: [...wallet[activeUser]?.slice(0, wallet[activeUser].length - 1)]
    }));

    setLetters((prev) => ({
      ...prev,
      [activeUser]: [...letters[activeUser], stampId]
    }));
    
    setTransactions((prev) => ({
      ...prev,
      [activeUser]: [...transactions[activeUser], transferRes, burnRes]
    }));
  };

  const handleBuyStamp = async () => {
    const mintRes = await mintStamp();
    const transferRes = await transferStamp(wallets['mailflow'], wallets[activeUser]);
    
    setWallet((prev) => ({
      ...prev,
      [activeUser]: [...wallet[activeUser], transferRes.functionArgs[3].substring(5)]
    }));

    setTransactions((prev) => ({
      ...prev,
      [activeUser]: [...transactions[activeUser], mintRes, transferRes]
    }));
  };

  const handleTransferStamp = async () => {
    const stampId = wallet[activeUser][wallet[activeUser].length - 1];
    if (!stampId) {
      alert("Please buy some stamps!");
      return;
    }
    const transferRes = await transferStamp(wallets[activeUser], wallets[transferReceiver], stampId);

    if ((typeof transferRes === 'object' && Object.keys(transferRes).length === 0) || transferRes.result?.type === 'err') {
      setTransactions((prev) => ({
        ...prev,
        [activeUser]: [...transactions[activeUser], transferRes],
      }));
      return;
    }

    setWallet((prev) => ({
      ...prev,
      [activeUser]: [...wallet[activeUser]?.slice(0, wallet[activeUser].length - 1)],
      [transferReceiver]: [...wallet[transferReceiver], stampId]
    }));

    setTransactions((prev) => ({
      ...prev,
      [activeUser]: [...transactions[activeUser] ?? [], transferRes],
      [transferReceiver]: [...transactions[transferReceiver], transferRes],
    }));
  };

  return (
    <div className="App" style={{ padding: "20px", fontFamily: "Arial" }}>
      <h1>MailFlow Mock-up</h1>

      {/* Select User */}
      <div style={{ marginBottom: "20px" }}>
        <label>User:</label>
        <select id="users" name="users" onChange={handleSelectUser} style={{ margin: "10px" }}>
          <option value="user1">user1</option>
          <option value="user2">user2</option>
        </select>
        <span>{`****${wallets[activeUser].substring(wallets[activeUser].length - 4)}`}</span>
      </div>

      {/* Wallet Section */}
      <div style={{ marginBottom: "20px" }}>
        <h2>Wallet</h2>
        {wallet[activeUser] === undefined || wallet[activeUser].length === 0 ? (
          <p>No stamps in your wallet.</p>
        ) : (
          <ul>
            {wallet[activeUser].map((id) => (
              <li key={id}>{id}</li>
            ))}
          </ul>
        )}
      </div>

      {/* Letters Section */}
      <div>
        <h2>Letters</h2>
        {letters[activeUser] === undefined || letters[activeUser].length === 0 ? (
          <p>No letters sent yet.</p>
        ) : (
          <ul>
            {letters[activeUser].map((id) => (
              <li key={id}>{id}</li>
            ))}
          </ul>
        )}
      </div>

      {/* Buttons Section */}
      <div>
        <hr style={{margin: "10px"}}/>
        <div>
          <button onClick={handleBuyStamp} style={{ margin: "4px" }}>Buy stamp</button>
        </div>
        <div>
          <button onClick={handleMailStamp} style={{ margin: "4px" }}>Mail letter</button>
        </div>
        <div>
          <button onClick={handleTransferStamp} style={{ margin: "4px" }}>Transfer stamp</button>
          <label>to</label>
          <select id="receivers" name="receivers" onChange={handleSelectReceiver} style={{ margin: "4px" }}>
            <option value="user1">user1</option>
            <option value="user2">user2</option>
          </select>
        </div>
      </div>

      {/* Blockchain Log Section */}
      <div>
        <h2>Blockchain Tx Log</h2>
        <table>
          <thead>
            <tr>
              <th>Function Name</th>
              <th>Arguments</th>
              <th>Result</th>
            </tr>
          </thead>
          <tbody>
            {(transactions[activeUser] ?? []).map((log, index) => (
              <tr key={'tx-' + index} className={index % 2 === 0 ? 'evenRow' : 'oddRow'}>
                <td>{log.functionName}</td>
                <td>
                  {Array.isArray(log.functionArgs)
                    ? JSON.stringify(log.functionArgs)
                    : log.functionArgs}
                </td>
                <td>
                  {typeof log.result === "object"
                    ? JSON.stringify(log.result)
                    : log.result}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default TestnetMockup;
